<template>
  <div >
    <div v-for="item in ele_content">
      <el-input :type="item.type" :name="item.name" :placeholder="item.placeholder"  v-if="item.type=='text'"/>
      <el-select v-if="item.type=='select'" v-model="item.value">
        <el-option
          :key="item_option.value"
          :label="item_option.value"
          :value="item_option.id"
          v-for="item_option in item.option"></el-option>
      </el-select>
      <el-radio-group v-if="item.type=='radio'" v-model="item.value">
        <el-radio :label="item_radio.value" :value="item_radio.id" v-for="item_radio in item.radio">{{ item_radio.name }}</el-radio>
      </el-radio-group>
      <el-switch v-model="item.value" v-if="item.type=='switch'" on-color="#13ce66" off-color="#ff4949"></el-switch>
      <el-time-select
        v-model="item.valuet" v-if="item.type=='time-select'"
        :picker-options="{
    start: '08:30',
    step: '00:15',
    end: '18:30'
  }"
        placeholder="选择时间">
      </el-time-select>
      <el-checkbox-group v-model="item.checkList[0]" v-if="item.type=='checkList'">
        <el-checkbox-button v-for="check in item.checkList" :label="check" :key="check">{{ check }}</el-checkbox-button>
      </el-checkbox-group>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'Edit',
    data () {
      return {
        ele_content: [],
        element: [
          {
            type: 'text',
            placeholder: '名字',
            name: 'name'
          },
          {
            type: 'textarea',
            placeholder: '描述',
            name: 'description'
          },
          {
            type: 'select',
            placeholder: '选择',
            name: 'description',
            value: '',
            option: [
              {
                id: '1',
                value: '111'
              },
              {
                id: '2',
                value: '222'
              }
            ]
          },
          {
            type: 'radio',
            value: '',
            active: false,
            radio: [
              {
                id: '1',
                value: '3',
                name: '备选',
                active: false
              },
              {
                id: '2',
                value: '4',
                name: '其他',
                active: false
              }
            ]
          },
          {
            type: 'switch',
            value: true
          },
          {
            type: 'time-select',
            valuet: ''
          },
          {
            type: 'checkList',
            checkList: ['上海', '北京', '长春']
          }
        ]
      }
    },
    mounted: function () {
      this.loaded()
    },
    // 在 `methods` 对象中定义方法
    methods: {
      loaded: function () {
        for (var i = 0; i < this.element.length; i++) {
          if (this.element[i].type === 'text') {
            this.ele_content.push(this.element[i])
          } else if (this.element[i].type === 'textarea') {
            this.ele_content.push(this.element[i])
          } else if (this.element[i].type === 'select') {
            this.ele_content.push(this.element[i])
          } else if (this.element[i].type === 'radio') {
            this.ele_content.push(this.element[i])
          } else if (this.element[i].type === 'switch') {
            this.ele_content.push(this.element[i])
          } else if (this.element[i].type === 'time-select') {
            this.ele_content.push(this.element[i])
          } else if (this.element[i].type === 'checkList') {
            this.ele_content.push(this.element[i])
//            console.log(this.ele_content.checkList[0])
          }
        }
      }
    }
  }

</script>

<style>
</style>



